<script>
	import mqtt from 'mqtt/dist/mqtt.js'
	import  {value_model,switch_model} from "model.js"
	export default {
		onLaunch: function() {
			console.log('App Launch')
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		},
		globalData:{
			value_model:value_model,
			switch_model:switch_model,	
			msgs:[{
				topic:"test_topic",
				msg:"test_msg"
			}],
			options:{
				keepalive:45,
				clientId:"user_1",
				clean:true,
				connectTimeout:30000,
				username:"user_1",
				password:"abc123",
				keepalive:10
				
			},
			// #ifdef H5
			broker:"ws://192.168.199.156:8083/mqtt",
			// #endif
			// #ifdef APP-PLUS
			broker:"wx://192.168.199.156:8083/mqtt",
			// #endif
			// #ifdef MP-WEIXIN
			broker:"wss://192.168.199.156:8084/mqtt",
			// #endif
			
			client:null,
			status:{
				connecting:false,
				connected:false
			}
		},
		methods:{
			setModelValue(msgjson){
				for(let i =0;i<getApp().globalData.value_model.length;i++){
					if(getApp().globalData.value_model[i].id==msgjson.id){
						getApp().globalData.value_model[i].value=msgjson.value
					}
				}
				
				for(let i =0;i<getApp().globalData.switch_model.length;i++){
					if(getApp().globalData.switch_model[i].id==msgjson.id){
						getApp().globalData.switch_model[i].value=msgjson.value
					}
				}
				
			},
			onConnected(){
				let client = getApp().globalData.client
				let msgs = getApp().globalData.msgs
				client.on("message",
					(topic, message) =>{
					  // message is Buffer
					  let msgjson=JSON.parse(message.toString())
					  console.log(msgjson)
					  if(topic=='post')this.setModelValue(msgjson)
					  msgs.push({
						  topic:topic,
						  msg:message
					  })
					}
				)
				
				client.subscribe('post',(err)=>{
					if(!err){
						uni.showToast({
							title:"订阅成功！",
							icon:"success"
						})
					}
				})
			},
			createConnection(){
				let globe = getApp().globalData
				globe.client=mqtt.connect(globe.broker,globe.options)
				globe.client.on("error",
					(error) =>{
					  uni.showToast({
					  	title:"连接错误！"
					  })
					  globe.connecting=false
					  console.log(error)
					}
				)
				globe.status.connecting=true;
				globe.client.on("connect",
					(res)=>{
						uni.showToast({
							title:"连接成功",
							duration:1000
						})
						// uni.hideLoading()
						console.log(res)
						globe.status.connected=true;
						globe.status.connecting=false;
						this.onConnected()
						
					},
					(err)=>{
						console.log(err)
					}
				)
			}
		},
		
		onLaunch() {			
			this.createConnection()
			
		},
		destroyed() {
			let client = getApp().globalData.client
			if(client !=null){
				client.end(false,()=>{
					uni.showLoading({
						title:"断开连接中..."
					})
					console.log("disconnect successfully")
					uni.hideLoading()
					uni.showToast({
						title:"disconnect successfully"
					})
				})
			}
		}
	}
</script>

